/* modules */
.breadcrumb {
  padding: 0;
  background: none;
  font-size: rem(13px);
  margin-bottom: 25px;
  font-family: $font-montserrat;
  &-item {
    a, span {
      line-height: 2.5;
      display: inline-block;
      text-decoration: none;
    }
    a {
      &:hover {
        text-decoration: underline;
      }
    }
    &::before {
      line-height: 2.5;
    }
    &.active {
      color: $primary-color;
    }
    &+.breadcrumb-item {
      padding-left: 5px;
      &::before {
        color: $primary-color;
        padding-right: 5px;
      }
    }
  }
  @include media-breakpoint-up(md) {
    font-size: rem(14px);
    margin-bottom: 34px;
  }
  @include media-breakpoint-up(xl) {
    margin-bottom: 25px;
    margin-top: -15px;
  }
  @include media-breakpoint-up(xxl) {
    font-size: rem(18px);
  }
}

.content {
  padding-top: 37px;
  padding-bottom: 50px;
  overflow: hidden;
  @include media-breakpoint-up(xl) {
    padding-top: 50px;
  }
  &__document {
    &.--about-us {
      h1, .h1 {
        position: relative;
        display: inline-block;
        &::before {
          width: 228px;
          height: 14px;
          background-color: $add-color;
          position: absolute;
          left: 26px;
          top: 16px;
          content: '';
          z-index: -1;
          @include media-breakpoint-up(sm) {
            width: 404px;
          }
          @include media-breakpoint-up(md) {
            width: 546px;
            left: 56px;
            top: 22px;
          }
        }
      } 
    }
    h2, .h2, h3, .h3 {
      display: inline-block;
      &::before {
        height: 14px;
        background-color: $add-color;
        position: absolute;
        left: 0;
        top: 12px;
        content: '';
        width: 82%;
        z-index: -1;
      }
    }
    h2, .h2 {
      &::before {
        @include media-breakpoint-up(md) {
          width: 118%;
          top: 18px;
          left: 5%;
        }
      }
    }
    h3, .h3 {
      &::before {
        width: auto;
        min-width: 193px;
        top: 3px;
        @include media-breakpoint-up(md) {
          min-width: 303px;
          top: 6px;
        }
      }
    }
    &-thumb {
      padding: 0 33px 30px 14px;
      position: relative;
      margin-bottom: 20px;
      img {
        background: #C4C4C4;
        box-shadow: 0px 4px 4px rgba(199, 160, 133, 0.2);
        position: relative;
        z-index: 5;
        width: 100%;
      }
      &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 26px;
        background-color: $add-color;
      }
      @include media-breakpoint-up(md) {
        padding: 0 25px 34px 11px;
        width: 260px;
        flex-shrink: 0;
        margin-right: 20px;
        margin-bottom: 0;
        &::before {
          top: 11px;
        }
        img {
          height: 224px;
          object-fit: cover;
        }
      }
    }
    &-head {
      .h1, h1 {
        &::before, &::after {
          height: 14px;
          background-color: $add-color;
          position: absolute;
          left: 0;
          top: 12px;
          content: '';
          width: 285px;
          z-index: -1;
        }
        &::after {
          top: auto;
          bottom: 5px;
          width: 240px;
          left: 10px;
        }
      }
      @include media-breakpoint-up(sm) {
        .h1, h1 {
          &::after {
            width: 350px;
          }
        }
      }
      @include media-breakpoint-up(md) {
        display: flex;
        align-items: flex-start;
        margin-bottom: 33px;
        .h1, h1 {
          font-size: rem(45px);
          margin-bottom: 0;
          &::before {
            top: 27px;
            left: 50px;
          }
          &::after {
            bottom: auto;
            top: 142px;
            left: 20px;
          }
        }
      }
    }
    &.--one-services {
      .h1, h1 {
        margin-bottom: 30px;
        &::before {
          height: 14px;
          background-color: $add-color;
          position: absolute;
          left: 56px;
          top: 16px;
          content: '';
          width: 244px;
          z-index: -1;
        }
      }
      @include media-breakpoint-up(sm) {
        .h1, h1 {
          &::before {
            width: 406px;
          }
        }
      }
      @include media-breakpoint-up(md) {
        .h1, h1 {
          &::before {
            width: 546px;
            top: 19px;
          }
        }
      }
    }
  }
  &__btn {
    width: 100%;
    display: block;
    @include media-breakpoint-up(md) {
      max-width: 350px;
      margin: 0 auto;
    }
    @include media-breakpoint-up(xl) {
      max-width: 305px;
    }
  }
}

.catalog-nav {
  text-transform: uppercase;
  font-size: rem(16px);
  &>ul {
    margin: 0;
    padding: 0;
    li {
      margin: 0;
      list-style: none;
      position: relative;
      background: $add-color;
      border-bottom: 1px solid rgba($color-white,.4);
      &>a, &>span {
        text-decoration: none;
        display: block;
        line-height: 1.2;
        color: $color-black;
        padding: 16px 50px 16px 20px;
      }
      [class*="icon-"] {
        position: absolute;
        width: 50px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        right: 0;
        top: 0;
        font-size: rem(20px);
        top: 0;
        z-index: 5;
        cursor: pointer;
        transform-origin: 50%;
        transform: rotate(0);
      }
      ul {
        text-transform: none;
        display: none;
        background: #F9F2ED;
        padding: 8px 0;
        li {
          background: transparent;
          border: 0;
          &>a, &>span {
            padding: 8px 50px 8px 34px;
          }
          [class*="icon-"] {
            height: 40px;
            line-height: 40px;
            top: 0;
          }
        }
        ul {
          background: $color-white;
          li {
            background: transparent;
            &>a, &>span {
              padding: 5px 50px 5px 55px;
            }
          }
        }
      }
      &.has-folder.active {
        &>[class*="icon-"] {
          transform: rotate(-180deg);
        }
      }
    }
  }
  @include media-breakpoint-up(xxl) {
    font-size: rem(20px);
    &>ul {
      li {
        [class*="icon-"] {
          top: 5px;
        }
      }
    }
  }
}

.services-nav {
  text-transform: uppercase;
  font-size: rem(18px);
  margin-bottom: 20px;
  &>ul {
    margin: 0;
    padding: 0;
    li {
      margin: 0;
      list-style: none;
      position: relative;
      background: $add-color;
      &>a, &>span {
        text-decoration: none;
        display: block;
        line-height: 1.2;
        color: $color-black;
        padding: 12px 50px 12px 70px;
        font-weight: 500;
      }
      [class*="icon-arrow-"] {
        position: absolute;
        width: 45px;
        height: 45px;
        line-height: 45px;
        text-align: center;
        right: 0;
        font-size: rem(20px);
        top: 0px;
        z-index: 5;
        cursor: pointer;
        transform-origin: 50%;
        transform: rotate(0);
      }
      [class*="icon-services-"] {
        position: absolute;
        font-size: rem(31px);
        color: rgba(68, 68, 68, 0.5);
        top: 7px;
        left: 20px;
      }
      ul {
        text-transform: none;
        display: none;
        background: #fff;
        padding: 0;
        li {
          background: transparent;
          &>a, &>span {
            padding: 8px 50px 8px 20px;
          }
          [class*="icon-"] {
            height: 40px;
            line-height: 40px;
            top: 0;
          }
          &>a {
            &:hover {
              background: #F9F2ED;
            }
          }
        }
        ul {
          background: $color-white;
          li {
            background: transparent;
            &>a, &>span {
              padding: 7px 50px 7px 20px;
            }
          }
        }
        &.ul-folder {
          li.active {
            &>a, &>span {
              background: #F9F2ED;
            }
          }
        }
      }
      &.has-folder {
        margin-bottom: 20px;
        &.active {
          &>[class*="icon-arrow-"] {
            transform: rotate(-180deg);
          }
        }
      }
    }
  }
}

.banner-row {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    top: 10px;
    right: 0;
    bottom: 10px;
    left: 0;
    background: $add-color;
  }
  &__block {
    position: relative;
    z-index: 5;
    overflow: hidden;
    img {
      display: block;
      min-height: 120px;
      object-fit: cover;
    }
    .h1, h1 {
      position: absolute;
      white-space: nowrap;
      line-height: 1;
      margin: 0;
      color: $color-white;
      bottom: 15px;
      left: 10px;
      font-size: rem(26px);
      text-shadow: 0px 4px 4px rgba($color-black,.25);
    }
  }
  @include media-breakpoint-up(sm) {
    &::before {
      top: 13px;
      bottom: 17px;
    }
    &__block {
      img {
        min-height: 220px;
      }
      .h1, h1 {
        bottom: 25px;
        left: 30px;
        font-size: rem(48px);
      }
    }
  }
  @include media-breakpoint-up(md) {
    &::before {
      top: 21px;
      bottom: 26px;
    }
    &__block {
      img {
        min-height: 346px;
      }
      .h1, h1 {
        font-size: rem(64px);
        bottom: 32px;
        left: 45px;
      }
    }
  }
  @include media-breakpoint-up(xl) {
    &::before {
      top: 33px;
      bottom: 34px;
    }
  }
}

.modal {
  text-align: center;
  pointer-events: none;
  &__wrapper {
    pointer-events: all;
    position: relative;
    background: $color-white;
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    max-width: 633px;
    width: 100%;
    padding: 20px 25px 50px 25px;
    box-shadow: 0px 10px 10px rgba($color-black,.25);
  }
  &__close {
    position: absolute;
    top: -50px;
    right: -10px;
    z-index: 10;
    width: 50px;
    height: 50px;
    background: transparent;
    &::before {
      font-size: rem(28px);
      font-family: $font-icomoon;
      color: $color-white;
      content: '\e901';
      display: inline-block;
      vertical-align: middle;
      line-height: 1;
    }
    &:hover {
      &::before {
        color: $primary-color;
      }
    }
  }
  &__head {
    font-size: rem(30px);
    font-family: $font-cormorant;
    color: $color-black;
    position: relative;
    font-weight: 600;
    text-align: center;
    z-index: 5;
    &::before {
      height: 14px;
      background-color: $add-color;
      position: absolute;
      right: 45px;
      top: 22px;
      content: '';
      width: 228px;
      z-index: -1;
    }
    .--senks & {
      font-size: 64px;
      font-weight: 500;
      line-height: 1.2;
      margin-bottom: 10px;
      display: inline-block;
      &::before {
        top: 46px;
      }
    }
  }
  &__content {
    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
    .--senks & {
      font-size: 20px;
      text-align: center;
      p {
        line-height: 28px;
      }
    }
  }
  @include media-breakpoint-up(sm) {
    &__wrapper {
      padding: 40px 45px 50px 45px;
      &.--senks {
        padding-top: 46px !important;
      }
    }
  }
  @include media-breakpoint-up(md) {
    &__head {
      font-size: rem(36px);
      &::before {
        left: -25px;
        right: auto;
        top: 24px;
      }
    }
    &__wrapper {
      padding: 70px 90px 70px 90px;
    }
  }
}

.mfp-bg-gallery {
  background: rgba(249, 242, 237, 0.7);
  opacity: 1;
}

.news {
  $this: &;
  &__row {
    margin: 0;
  }
  &__block {
    margin-bottom: 55px;
    &-thumb {
      padding: 15px 0 10px 38px;
      position: relative;
      width: 300px;
      margin: 0 auto 20px;
      img {
        background: #C4C4C4;
        box-shadow: 0px 4px 4px rgba(199, 160, 133, 0.2);
        position: relative;
        z-index: 5;
        width: 253px;
        height: 253px;
        object-fit: cover;
      }
      &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 34px;
        bottom: 0;
        top: 0;
        background-color: $add-color;
      }
    }
    &-content {
      font-size: rem(16px);
      p {
        line-height: 1.2;
        display: -webkit-box;
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      h2 {
        font-family: $font-cormorant;
        font-weight: 500;
        font-size: rem(30px);
        line-height: 36px;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        a {
          color: $color-black;
          text-decoration: none;
          &:hover {
            color: $add-color-dark;
          }
        }
        &::before, &::after {
          height: 14px;
          background-color: $add-color;
          position: absolute;
          left: 0;
          top: 12px;
          content: '';
          width: 285px;
          z-index: -1;
        }
        &::after {
          top: auto;
          bottom: 5px;
          width: 240px;
          left: 10px;
        }
      }
    }
    &-more {
      text-decoration: none;
      color: #787878;
      font-size: rem(16px)
    }
    &.--first {
      #{$this} {
        &__block {
          &-thumb {
            padding: 0 33px 30px 14px;
            &::before {
              content: '';
              position: absolute;
              left: 0;
              right: 0;
              bottom: 0;
              top: 26px;
              background-color: $add-color;
            }
          }
          &-content {
            p {
              display: block;
              &+p {
                line-height: 1.2;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                text-overflow: ellipsis;
              }
            }
          }
        }
      }
    }
  }
  @include media-breakpoint-up(sm) {
    &__block {
      &-content {
        h2 {
          &::before {
            display: none;
          }
          &::after {
            width: 350px;
            left: auto;
            right: 0;
            bottom: 9px;
          }
        }
      }
      &.--first {
        #{$this} {
          &__block {
            &-content {
              h2 {
                &::before {
                  display: block;
                }
                &::after {
                  top: auto;
                  bottom: 5px;
                  width: 240px;
                  left: 10px;
                }
              }
            }
          }
        }
      }
    }
  }
  @include media-breakpoint-up(md) {
    &__row {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -10px;
      background-image: url('data:image/png;base64,');
      background-position: calc(100% - 50px) 449px;
      background-repeat: no-repeat;
    }
    &__block {
      margin: 0 10px 60px 10px;
      width: calc(50% - 20px);
      &.--first, &.--left {
        width: calc(100% - 20px);
        display: flex;
        align-items: flex-start;
        #{$this} {
          &__block {
            &-thumb {
              padding: 0 25px 34px 11px;
              width: 350px;
              flex-shrink: 0;
              margin-right: 20px;
              margin-bottom: 0;
              &::before {
                top: 11px;
                right: 0;
              }
            }
            &-content {
              h2, .h2 {
                font-size: rem(36px);
                &::before {
                  top: 20px;
                  left: 50px;
                }
                &::after {
                  bottom: auto;
                  top: 90px;
                  left: 20px;
                  width: 350px;
                }
              }
              p {
                line-height: 24px;
              }
            }
          }
        }
      }
      &.--center, &.--right {
        #{$this} {
          &__block {
            &-thumb {
              margin-bottom: 15px;
              img {
                height: 290px;
                object-fit: cover;
              }
            }
            &-content {
              h2, .h2 {
                font-weight: bold;
                font-size: rem(20px);
                line-height: 1.2;
                &::before, &::after {
                  display: none;
                }
              }
              p {
                line-height: 1.4;
              }
            }
          }
        }
      }
      &.--center {
        #{$this} {
          &__block {
            &-thumb {
              padding: 20px 0 17px 60px;
              &::before {
                right: 26px;
              }
            }
          }
        }
      }
      &.--right {
        #{$this} {
          &__block {
            &-thumb {
              padding: 0px 40px 33px 0;
              &::before {
                left: 20px;
                right: 0;
                top: 20px;
              }
            }
          }
        }
      }
      &-thumb {
        width: auto;
        img {
          width: 314px;
          height: 314px;
        }
      }
    }
  }
  @include media-breakpoint-up(lg) {
    &__block {
      width: calc(33.3% - 20px);
      &.--first {
        width: calc(100% - 20px);
      }
      &.--left {
        width: calc(33.3% - 20px);
        display: block;
        #{$this} {
          &__block {
            &-thumb {
              width: 100%;
              margin-bottom: 15px;
              padding: 20px 0 17px 60px;
              &::before {
                right: 26px;
                top: 0;
              }
            }
            &-content {
              font-size: rem(16px);
              h2, .h2 {
                font-weight: bold;
                font-size: rem(20px);
                line-height: 1.2;
                &::before, &::after {
                  display: none;
                }
              }
              p {
                line-height: 1.4;
              }
            }
          }
        }
      }
      &.--center {
        #{$this} {
          &__block {
            &-thumb {
              padding: 0px 40px 33px 0;
              &::before {
                left: 20px;
                right: 0;
                top: 20px;
              }
              img {
                height: 253px;
              }
            }
          }
        }
      }
      &.--right {
        #{$this} {
          &__block {
            &-thumb {
              padding: 17px 63px 20px 0;
              &::before {
                left: 23px;
                right: 0;
                top: 0px;
              }
            }
          }
        }
      }
    }
  }
  @include media-breakpoint-up(xl) {
    &__block {
      &-thumb {
        &::before {
          transition: all 0.35s;
          box-shadow: inset 0 0 0 0px #f1e1d5;
        }
      }
      &:hover {
        .news__block-thumb::before {
          background: $color-white;
          box-shadow: inset 0 0 0 10px #f1e1d5;
        }
      }
      &.--center {
        #{$this} {
          &__block {
            &-thumb {
              img {
                height: 230px;
              }
            }
          }
        }
      }
    }
  }
  @include media-breakpoint-up(xxl) {
    &__block {
      &.--center {
        #{$this} {
          &__block {
            &-thumb {
              img {
                height: 264px;
              }
            }
          }
        }
      }
    }
  }
}

.our-team {
  .h1, h1 {
    font-size: rem(36px);
    &::before {
      height: 14px;
      background-color: $add-color;
      position: absolute;
      left: 66px;
      top: 25px;
      content: '';
      width: 139px;
      z-index: -1;
    }
  }
  &__carousel {
    padding: 0;
    .slick-dots {
      left: 0;
      
      li {
        width: 8px;
        height: 8px;
        margin: 0 7px;
        button {
          width: 8px;
          padding: 0;
          height: 8px;
          &::before {
            width: 8px;
            height: 8px;
            content: '';
            border-radius: 50%;
            border: 1px solid #F1E1D5;
            opacity: 1;
          }
        }
        &.slick-active {
          button {
            &::before {
              background: #F1E1D5;
              opacity: 1;
            }
          }
        }
      }
    }
    .slick-arrow {
      width: 40px;
      height: 40px;
      display: none !important;
      &::before {
        font-family: $font-icomoon;
        color: #4B4A4A;
        font-size: rem(40px);
        opacity: 1;
      }
    }
    .slick-next {
      right: -10px;
      &::before {
        content: '\e90b';
      }
    }
    .slick-prev {
      left: -10px;
      &::before {
        content: '\e90c';
      }
    }
  }
  &__block {
    padding: 0 20px;
    position: relative;
    text-align: center;
    font-size: rem(16px);
    overflow: hidden;
    &::before {
      content: '';
      z-index: -1;
      background: $add-color;
      left: 0;
      top: 60px;
      right: 0;
      bottom: 0;
      position: absolute;
    }
    &-thumb {
      img {
        max-width: 100%;
        height: auto;
        display: block;
        box-shadow: 0px 4px 4px rgba(199, 160, 133, 0.2);
        margin: 0 auto;
        position: relative;
        z-index: 5;
      }
      &::before, &::after {
        position: absolute;
        content: '';
        background-image: url('data:image/png;base64,');
        background-position: 0 0;
        background-repeat: no-repeat;
        width: 116px;
        height: 96px;
      }
      &::before {
        top: 95px;
        right: -10px;
      }
      &::after {
        top: 195px;
        left: -10px;
      }
    }
    &-content {
      min-height: 112px;
      padding: 10px 0px 45px 0px;
