/* pagination */
.pagination {
  $this: &;
  font-size: rem(16px);
  text-align: left;
  margin-bottom: 40px;
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    width: 100%;
    &>li {
      list-style: none;
      margin: 0;
      display: inline-block;
      line-height: 1;
      vertical-align: middle;
      &#{$this} {
        &__prev, &__next {
          font-size: rem(18px);
          &>a, &>span {
            border-color: #F6DBC8;
            background-color: #F6DBC8;
            &::before {
              font-family: $font-icomoon;
              vertical-align: -2px;
              line-height: 1;
              display: inline-block;
            }
          }
        }
        &__prev {
          margin-right: 0px;
          &>a, &>span {
            &::before {
              content: '\e914';
            }
          }
        }
        &__next {
          margin-left: 8px;
          &>a, &>span {
            &::before {
              content: '\e915';
            }
          }
        }
      }
      &+li {
        margin-left: 8px;
      }
      &>a, &>span {
        display: inline-block;
        width: 32px;
        height: 32px;
        text-align: center;
        line-height: 31px;
        color: $color-black;
        text-decoration: none;
        border-radius: 0;
        border: 1px solid transparent;
        background: #F9F2ED;
        transform: translateY(0px);
        position: relative;
        z-index: 1;
        overflow: hidden;
        vertical-align: middle;
      }
      &>a {
        &:hover {
          z-index: 2;
          background: $primary-color;
          color: $color-white;
          border-color: $primary-color;
          transform: translateY(0px);
        }
      }
      &.active {
        &>a, &>span {
          background: #F1E1D5;
          color: $primary-color;
          border-color: #F1E1D5;
        }
      }
      &.disabled {
        &>a, &>span {
          cursor: auto;
          border-color: rgba($color-black,.2);
          color: rgba($color-black,.3);
          box-shadow: none;
          background: transparent;
          transform: translateY(0);
        }
      }
    }
  }
  &--center {
    ul {
      justify-content: center;
    }
  }
  &--right {
    ul {
      justify-content: flex-end;
    }
  }
  &--justify {
    ul {
      justify-content: center;
      li {
        flex-shrink: 0;
      }
    }
    #{$this} {
      &__prev {
        flex-grow: 1;
      }
      &__next {
        flex-grow: 1;
        text-align: right;
      }
    }
  }
  @include media-breakpoint-up(xl) {
    margin-bottom: 50px;
  }
}

