.main-slider {
  position: relative;
  z-index: 1;
  margin-bottom: 36px;
  &__bg {
    position: absolute;
    background-repeat: no-repeat;
    background-position: 0 0;
    &.--bg1 {
      width: 114px;
      height: 106px;
      background-image: url('data:image/png;base64,');
      top: -15px;
    }
    &.--bg2 {
      width: 86px;
      height: 84px;
      background-image: url('data:image/png;base64,');
      top: 69px;
      right: 0;
    }
    &.--bg3 {
      top: 249px;
      right: 31px;
      width: 138px;
      height: 121px;
      background-image: url('data:image/png;base64,');
    }
  }
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    left: 0;
    top: 44px;
    right: 0;
    bottom: 0;
    background: $add-color;
  }
  &__carousel {
    z-index: 5;
    .slick-dots {
      bottom: auto;
      top: 330px;
      li {
        height: auto;
        width: auto;
        button {
          height: 6px;
          width: 6px;
          border: 1px solid $color-white;
          border-radius: 50%;
          padding: 0;
          &::before {
            display: none;
          }
        }
        &.slick-active {
          button {
            background: $color-white;
          }
        }
      }
    }
  }
  &__block {
    &-img {
      width: 248px;
      margin: 0 auto 40px;
      img {
        width: 100%;
        height: auto;
        display: block;
      }
    }
    &-content {
      text-align: center;
      padding-bottom: 45px;
      .h1, h1 {
        font-size: rem(30px);
      }
    }
  }
  &__btn {
    width: 220px;
    height: 42px;
    line-height: 42px;
    text-align: center;
    box-shadow: 1px 4px 5px rgba(0, 0, 0, 0.1);
    font-size: rem(18px);
    background: $color-white;
  }
  @include media-breakpoint-up(md) {
    margin-bottom: 60px;
    &::before {
      bottom: 34px;
      top: 45px;
    }
    &__block {
      display: flex;
      align-items: center;
      padding-right: 10px;
      &-img {
        order: 2;
        flex-shrink: 0;
        width: 349px;
        margin: 0;
      }
      &-content {
        order: 1;
        text-align: left;
        flex-grow: 1;
        margin: 0;
        padding: 0 15px 40px 0;
        position: relative;
        top: 40px;
      }
    }
    &__carousel {
      .slick-dots {
        right: -10px;
        width: 10px;
        top: 50%;
        transform: translateY(-50%);
        text-align: center;
        display: flex;
        flex-direction: column;
        li {
          margin: 5px 0;
        }
      }
    }
    &__bg {
      &.--bg1 {
        width: 122px;
        height: 121px;
        top: -30px;
        background-image: url('data:image/png;base64,');
      }
      &.--bg2 {
        width: 147px;
        height: 139px;
        background-image: url('data:image/png;base64,');
        left: 234px;
        top: auto;
        bottom: -50px;
      }
      &.--bg3 {
        display: none;
      }
    }
  }
  @include media-breakpoint-up(lg) {
    margin-bottom: 85px;
    &::before {
      bottom: 47px;
      top: 62px;
    }
    &__block {
      &-img {
        width: 448px;
      }
    }
    &__btn {
      height: 50px;
      line-height: 50px;
      width: 264px;
    }
    &__bg {
      &.--bg1 {
        width: 155px;
        height: 154px;
        top: -30px;
        background-image: url('data:image/png;base64,');
      }
      &.--bg2 {
        width: 186px;
        height: 176px;
        left: 285px;
        top: auto;
        bottom: -50px;
        background-image: url('data:image/png;base64,');
      }
      &.--bg3 {
        display: none;
      }
    }
    &__container {
      max-width: 940px;
    }
  }
  @include media-breakpoint-up(xl) {
    &::before {
      top: 73px;
      bottom: 110px;
    }
    &__container {
      max-width: inherit;
    }
    &__block {
      max-width: 1160px;
      margin: 0 auto;
      &-content {
        top: 0;
        h1, .h1 {
          font-size: rem(45px);
          max-width: 566px;
        }
      }
    }
    &__bg {
      &.--bg1 {
        top: -20px;
      }
      &.--bg2 {
        width: 284px;
        height: 251px;
        background-size: 284px 251px;
        left: 685px;
        bottom: -47px;
      }
    }
    &__carousel {
      .slick-dots {
        right: 10px;
        li {
          width: 14px;
          margin: 10px 0;
          button {
            width: 14px;
            height: 14px;
          }
        }
      }
    }
  }
  @include media-breakpoint-up(xxl) {
    margin-bottom: 95px;
    &::before {
      bottom: 134px;
      top: 73px;
    }
    &__block {
      max-width: 1298px;
      margin: 0 auto;
      &-img {
        width: 571px;
      }
      &-content {
        .h1, h1 {
          font-size: rem(60px);
        }
      }
    }
    &__btn {
      height: 75px;
      line-height: 75px;
      width: 302px;
    }
    &__bg {
      &.--bg1 {
        width: 211px;
        height: 215px;
        top: auto;
        bottom: 140px;
        background-image: url('data:image/png;base64,');
      }
      &.--bg2 {
        width: 215px;
        height: 211px;
        left: auto;
        right: 202px;
        top: 80px;
        bottom: auto;
        background-image: url('data:image/png;base64,');
      }
      &.--bg3 {
        display: block;
        background-image: url('data:image/png;base64,');
        width: 369px;
        height: 360px;
        bottom: -80px;
        top: auto;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    &__carousel {
      .slick-dots {
        right: 20px;
        li {
          width: 14px;
          margin: 10px 0;
          button {
            width: 14px;
            height: 14px;
          }
        }
      }
    }
    &__container {
      max-width: inherit;
    }
  }
}

